@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
.thumbs{display:flex;align-items:flex-end;margin:0 auto;justify-content:center}.thumb{background-position:center;background-size:contain;border:2px solid #323232;margin:8px;cursor:pointer}.thumb.selected{border-color:red}.editor canvas{background-color:black}.editor .edit-inputs{display:flex;flex-direction:column;justify-content:center;align-items:center}.editor .edit-inputs input{display:block;width:60%}.App{font-family:sans-serif;text-align:center;margin:6rem 0;box-sizing:border-box}body{background:#d1913c;background:-webkit-gradient(linear, left bottom, left top, from(#d1913c), to(#ffd194));background:linear-gradient(to top, #d1913c, #ffd194);background-size:cover;background-repeat:no-repeat;min-height:100vh}.container{background-color:rgba(255,255,255,0.7);max-width:700px;margin:0 auto;padding:2rem}h1{font-family:'Yellowtail', serif;font-size:40px}input{font-size:18px;line-height:20px;padding:8px;margin:8px}button{display:inline-block;padding:0.5em 3em;background-color:transparent;cursor:pointer;border:solid 4px #323232;font-weight:bold;line-height:24px}button:hover{-webkit-transition:0.3s;transition:0.3s;background-color:#d3923e;color:#FFF}

